import React from 'react';

const ProfileHeader = ({ allOnboardingSteps = [], sortOrder, handleNextPreOnClick }) => {

    const sortedSteps = [...allOnboardingSteps].sort((a, b) => a.sort_order - b.sort_order);
    const currentIndex = sortedSteps.findIndex(step => step.sort_order === sortOrder);

    const currentStep = sortedSteps[currentIndex];
    const previousStep = sortedSteps[currentIndex - 1];
    const nextStep = sortedSteps[currentIndex + 1];

    return (
        <div className="detailMain">
    
            
                <div className="personalDetail d-flex align-items-center">
                {previousStep&& (
                    <span
                        onClick={() => handleNextPreOnClick(previousStep.short_name, previousStep.sort_order)}
                        style={{ cursor: 'pointer' }}
                    >
                        <img src="images/previousIcon-blue.svg" width="24" alt="Previous Icon" />
                    </span>
                    )}
                    <div className="personalLeft ms-2">
                        <span>{currentStep?.name}</span>
                        <p>{currentStep?.description}</p>
                    </div>
                </div>
            
            {nextStep && (
                <div className="personalDetail d-flex align-items-center">
                    <div className="personalLeft border-0 me-2 text-end">
                        <span>NEXT</span>
                        <p className="m-0">{nextStep.name}</p>
                    </div>
                    <span
                        onClick={() => handleNextPreOnClick(nextStep.short_name, nextStep.sort_order)}
                        style={{ cursor: 'pointer' }}
                    >
                        <img src="images/nextIconblue.svg" width="24" alt="Next Icon" />
                    </span>
                </div>
            )}
        </div>
    );
};

export default ProfileHeader;
