import React from 'react'
import ProfileHeader from './ProfileHeader'
import { FamilyDetails } from '../../employee/onboarding-process'

const FamilyDetail = (props) => {
    const { openModel,
        handleOpenClose,
        handleNextPreOnClick,
        profileData,
        addFamilyDetails,
        sortOrder,
        allOnboardingSteps,
        modeProfileModals
    } = props




    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="familyDetails"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Family Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("family_details")}
                    />

                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <ProfileHeader
                            sortOrder={sortOrder}
                            allOnboardingSteps={allOnboardingSteps}
                            handleNextPreOnClick={handleNextPreOnClick}
                        />
                        <FamilyDetails
                            profileData={profileData}
                            modeProfileModals={modeProfileModals}
                            addFamilyDetails={addFamilyDetails}
                        />

                    </div>

                </div>
                <div className="offcanvas-footer">

                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default FamilyDetail
