import React from 'react'
import { dateFormate } from '../../utils/common'

const ProfileInfo = (props) => {
    const {profileData}=props
    return (
        <>
            <div class="row px-md-1">
                <div class="col-md-4 mb-3 px-md-2">
                    <div class="cardBox h-100">
                        <div class="cardHead">
                            <h3>Profile Info.</h3>
                        </div>
                        <div class="d-flex gap-2">
                            <div class="profileImg">
                                <figure>
                                    <img src={profileData?.profile_img?profileData?.profile_img:"images/profile2.png"} alt="" />
                                </figure>
                            </div>
                            <div class="profileInfoTxt ms-1">
                                <span class="statusSpan text-capitalize">{profileData?.account_status}</span>
                                <strong>{profileData?.name}</strong>
                                <p>{profileData?.department_name}</p>

                                <span><img src="images/mailIcon.svg" alt="" width="16" /> {profileData?.email}</span>
                            </div>
                        </div>
                        <div class="d-flex gap-2 border-top pt-3 mt-3">
                            <div class="w-50">
                                <input type="file" class="d-none" id="uploadFileGr" />
                                <label htmlFor="uploadFileGr" class="btn btn-primary gradientBg w-100 px-2 py-2"><i
                                    class="bi bi-camera-fill me-1"></i> Upload Photo</label>
                            </div>
                            <div class="w-50">
                                <button type="button" class="btn btn-outline-danger fs-8 w-100 px-2 py-2"><i
                                    class="bi bi-dash-circle-fill me-1"></i> Remove Image</button>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-8 px-md-2">
                    <div class="row px-md-1">
                        <div class="col-md-4 mb-3 px-md-2">
                            <div class="cardBox infotxtbox">
                                <div class="d-flex justify-content-between">
                                    <span>Employee ID</span>
                                    <img src="images/employee-ID.svg" alt="" width="22" height="22" />
                                </div>
                                <strong>{profileData?.emp_id}</strong>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3 px-md-2">
                            <div class="cardBox infotxtbox">
                                <div class="d-flex justify-content-between">
                                    <span>Joining Date</span>
                                    <img src="images/joining-date.svg" alt="" width="22" height="22" />
                                </div>
                                <strong>{dateFormate(profileData?.joining_date)}</strong>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3 px-md-2">
                            <div class="cardBox infotxtbox">
                                <div class="d-flex justify-content-between">
                                    <span>Mobile No.</span>
                                    <img src="images/mobile-no.svg" alt="" width="22" height="22" />
                                </div>
                                <strong>{profileData?.mobile}</strong>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3 px-md-2">
                            <div class="cardBox infotxtbox">
                                <div class="d-flex justify-content-between">
                                    <span>Department</span>
                                    <img src="images/department.svg" alt="" width="22" height="22" />
                                </div>
                                <strong>{profileData?.department_name}</strong>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3 px-md-2">
                            <div class="cardBox infotxtbox">
                                <div class="d-flex justify-content-between">
                                    <span>Reporting Manager</span>
                                    <img src="images/reporting-manager.svg" alt="" width="22" height="22" />
                                </div>
                                <strong>{profileData?.reported_to_name}</strong>
                            </div>
                        </div>
                        <div class="col-md-4 mb-3 px-md-2">
                            <div class="cardBox infotxtbox">
                                <div class="d-flex justify-content-between">
                                    <span>Total Experience</span>
                                    <img src="images/total-experience.svg" alt="" width="22" height="22" />
                                </div>
                                <strong>-</strong>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ProfileInfo
