import React, { useEffect, useState } from 'react'
import { DocumentDetails, FullScreenPreviewModal } from '../../employee/onboarding-process';
import { CustomButton } from '../../globalComponents';
import ProfileHeader from './ProfileHeader';

const DocumentDetail = (props) => {
  const { openModel, handleOpenClose, handleNextPreOnClick,allEmployeeDocs,uploadDocuments,allOnboardingSteps,sortOrder,modeProfileModals,  } = props

  return (
    <>
      <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
        tabIndex={-1}
        style={{
          visibility: openModel ? 'visible' : 'hidden',
        }}
        id="documentDetails"
      >
        <div className="offcanvas-header">
          <h5 className="offcanvas-title fs-6 fw-bold text-primary">Document Details</h5>
          <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
            onClick={() => handleOpenClose("upload_documents")}
          />
        </div>
        <div className="offcanvas-body pt-0 mt-3">
          <div className="formPanel">
            <ProfileHeader
              sortOrder={sortOrder}
              allOnboardingSteps={allOnboardingSteps}
              handleNextPreOnClick={handleNextPreOnClick}
            />
        <DocumentDetails
        uploadDocuments={uploadDocuments}
        allEmployeeDocs={allEmployeeDocs}
        modeProfileModals={modeProfileModals}
        />

          </div>
        </div>
      </div>
      {openModel && <div className="modal-backdrop fade show"></div>}
    </>
  )
}

export default DocumentDetail
