import React, { useState } from 'react'
import { Input, Label } from '../../commanComponent'
import { calculateFilledPercentage } from '../../../utils/common';
import { validateSchema } from '../../../utils/ValidationHelper';
import { addReferenceDetailsSchema } from '../../../utils/ValidationSchema';
import ProfileHeader from './ProfileHeader';
import { ReferenceDetails } from '../../employee/onboarding-process';

const ReffrenceDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick,profileData,modeProfileModals,sortOrder,allOnboardingSteps,addReferenceDetails } = props
   
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="reffrenceDetails"
            >

                <div class="offcanvas-header">
                    <h5 class="offcanvas-title  fs-6 fw-bold text-primary">Reference Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("reference_details")}
                    />

                </div>
                <div class="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                    <ProfileHeader
                            sortOrder={sortOrder}
                            allOnboardingSteps={allOnboardingSteps}
                            handleNextPreOnClick={handleNextPreOnClick}
                        />

                       <ReferenceDetails
                       profileData={profileData}
                       modeProfileModals={modeProfileModals}
                       addReferenceDetails={addReferenceDetails}
                       />
                    </div>

                </div>
                <div class="offcanvas-footer">
                    {/* <div class="d-flex align-items-center justify-content-end gap-3">
                        <a href="#" class="btn btn-org-gradient"> update</a>
           
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default ReffrenceDetail
