import React, { useEffect, useState } from 'react'
import { Icon, Input, Label } from '../../commanComponent'
import { CustomError } from '../../globalComponents'
import EducationDetailsTable from '../../employee/onboarding-process/EducationDetailsTable'
import ApproveRejectModal from '../ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addEducationDetailsSchema } from '../../../utils/ValidationSchema'
import { calculateFilledPercentage } from '../../../utils/common'
import ProfileHeader from './ProfileHeader'
import { EducationDetails } from '../../employee/onboarding-process'

const EducationDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick, profileData, sortOrder, allOnboardingSteps,modeProfileModals,addEductionDetails} = props

    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="educationDetails"
            >

                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Education Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("education_details")}
                    />

                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">

                        <ProfileHeader
                            sortOrder={sortOrder}
                            allOnboardingSteps={allOnboardingSteps}
                            handleNextPreOnClick={handleNextPreOnClick}
                        />
                        <EducationDetails
                            profileData={profileData}
                            modeProfileModals={modeProfileModals}
                            addEductionDetails={addEductionDetails}
                        />
                    </div>

                </div>
                <div className="offcanvas-footer">
                    {/* <div className="d-flex align-items-center justify-content-end">
                        <a href="#" className="btn btn-org-gradient"> Edit</a>
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default EducationDetail
