import React, { useState } from 'react'
import ProfileHeader from './ProfileHeader';
import { SocialDetails } from '../../employee/onboarding-process';

const SocialDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick, allOnboardingSteps, sortOrder,
        profileData,modeProfileModals,addSocialDetails } = props


    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="socialDetails"
            >
                <div class="offcanvas-header">
                    <h5 class="offcanvas-title  fs-6 fw-bold text-primary">Social Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("social_details")}
                    />

                </div>
                <div class="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <ProfileHeader
                            sortOrder={sortOrder}
                            allOnboardingSteps={allOnboardingSteps}
                            handleNextPreOnClick={handleNextPreOnClick}
                        />

                       <SocialDetails
                       profileData={profileData}
                       modeProfileModals={modeProfileModals}
                       addSocialDetails={addSocialDetails}
                       />
                    </div>

                </div>
                <div class="offcanvas-footer">
                    {/* <div class="d-flex align-items-center justify-content-end">
                        <a href="#" class="btn btn-secondary"> Update</a>
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default SocialDetail
