import React, { useEffect, useState } from 'react'
import businesshiring from "../../images/business-hiring.json"
import Lottie from 'react-lottie-player'
import { CustomButton } from '../globalComponents'

const ProfileEditDocCard = ({ allOnboardingSteps, handleOpenCloseModal }) => {
    const [stepsObj, setStepsObj] = useState('')

    useEffect(() => {
        if (allOnboardingSteps && allOnboardingSteps?.length > 0) {
            setStepsObj(allOnboardingSteps[0])
        }
    }, [allOnboardingSteps])
    return (
        <>
            <div className="cardBox mb-3 createNewMrf">
                <div className="row align-items-center">
                    <div className="col-md-9 col-xl-10">
                        <div className="row align-items-center">
                            <div className="col-md-3">

                                <Lottie
                                    loop
                                    // src="../images/star-icon.json"
                                    animationData={businesshiring}
                                    background="transparent"
                                    speed="1"
                                    play
                                    style={{ width: "100%", height: "124px" }}
                                    autoPlay
                                />



                            </div>
                            <div className="col-md-9">
                                <div className="cardHead pb-0">
                                    <h3>Edit <span>Documents</span></h3>
                                    <p className="mb-0">This section contains important documents relevant to our operations. Each document is carefully curated to provide essential information for reference, compliance, or internal use.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-3 col-xl-2">
                        <CustomButton
                            handleClick={() => handleOpenCloseModal(stepsObj?.short_name, stepsObj.sort_order, "edit")}
                            text={"Edit Document"}
                            classNames="btn btn-primary gradientBg w-100"
                            slug={'edit_document'}
                        />  
                    </div>
                </div>
            </div>
        </>
    )
}

export default ProfileEditDocCard
