import React from 'react'

const IdCard = (props) => {
  const { openModel, handleOpenClose } = props
  return (
    <>
      <div className="modal fade attendanceViewodal" id="cardId" style={{ display: openModel ? "unset !important" : "none !important", opacity: openModel ? "unset" : "0" }}>

        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content rounded-0 border-0">
            <div className="modal-header border-0">
              <h1 className="modal-title fs-5 fw-bold text-primary">ID Card Details</h1>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                onClick={() => handleOpenClose("idCard")}
              />
            </div>
            <div className="modal-body mx-auto">
              <div
                style={{
                  width: "240px",
                  height: "350px",
                  backgroundImage: "url(./images/cardfront.svg)",
                  backgroundSize: "contain",
                  backgroundRepeat: "no-repeat",
                  boxShadow: "0px 0px 6px #00000029",
                  margin: "auto",
                  padding: "10px",
                  borderRadius: "4px",
                }}
              >
                <div>
                  <figure>
                    <img src="images/logoid-card.png" alt="" width="42" />
                  </figure>
                  <div style={{ width: "80px", height: "80px", margin: "auto" }}>
                    <img
                      src="images/profile-idcard.png"
                      alt=""
                      style={{
                        border: "1px solid #000",
                        borderRadius: "50%",
                        width: "80px",
                        height: "80px",
                      }}
                    />
                  </div>
                  <strong
                    style={{
                      display: "block",
                      fontSize: "18px",
                      fontWeight: "800",
                      textAlign: "center",
                      color: "#0B0B5F",
                      paddingTop: "20px",
                      paddingBottom: "16px",
                    }}
                  >
                    NIKITA PRAJAPAT
                  </strong>
                  <div>
                    <span
                      style={{
                        fontSize: "14px",
                        display: "block",
                        fontWeight: "600",
                        color: "#0B0B5F",
                        marginBottom: "6px",
                      }}
                    >
                      <b style={{ minWidth: "130px", display: "inline-block", textAlign: "left" }}>
                        Employee ID
                      </b>
                      : QD2484
                    </span>
                    <span
                      style={{
                        display: "block",
                        fontSize: "14px",
                        fontWeight: "600",
                        color: "#0B0B5F",
                        marginBottom: "6px",
                      }}
                    >
                      <b style={{ minWidth: "130px", display: "inline-block", textAlign: "left" }}>
                        Blood Group
                      </b>
                      : B+
                    </span>
                    <span
                      style={{
                        display: "block",
                        fontSize: "14px",
                        fontWeight: "600",
                        color: "#0B0B5F",
                        marginBottom: "6px",
                      }}
                    >
                      <b style={{ minWidth: "130px", display: "inline-block", textAlign: "left" }}>
                        Emergency No.
                      </b>
                      : 9012345678
                    </span>
                    <span
                      style={{
                        display: "block",
                        fontSize: "14px",
                        fontWeight: "600",
                        color: "#0B0B5F",
                        marginBottom: "6px",
                      }}
                    >
                      <b style={{ minWidth: "130px", display: "inline-block", textAlign: "left" }}>
                        Contact No.
                      </b>
                      : 8912345678
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {openModel && <div className="modal-backdrop fade show"></div>}
    </>
  )
}

export default IdCard
