import React, { useEffect, useState } from 'react'
import { ProfileInfo, ProfileEditDocCard, ProfileDetailCards } from '.'
import {
  ProfileDetail,
  FamilyDetail,
  EducationDetail,
  WorkExpDetail,
  SocialDetail,
  ReffrenceDetail,
  IdCard,
  DocumentDetail,
  BankDetail,
  Policies,
} from '../modal/profile'
import { useDispatch, useSelector } from 'react-redux'
import { dropdownMasterActions, employeeActions, onboardingActions, policiesActions } from '../../core/resource/actions'
import { setFilterDropdownData } from '../../utils/common'
const Profile = () => {
  const dispatch = useDispatch()
  const {
    getProfilePending,
    getProfileRes,
    getProfileError,

  } = useSelector(state => state.employee)

  const {

    addPersonalDetailsPending,
    addPersonalDetailsRes,
    addPersonalDetailsError,

    addFamilyDetailsPending,
    addFamilyDetailsRes,
    addFamilyDetailsError,

    addEducationDetailsPending,
    addEducationDetailsRes,
    addEducationDetailsError,

    addBankDetailsPending,
    addBankDetailsRes,
    addBankDetailsError,

    addSocialDetailsPending,
    addSocialDetailsRes,
    addSocialDetailsError,

    addWorkExperienceDetailsPending,
    addWorkExperienceDetailsRes,
    addWorkExperienceDetailsError,

    addReferenceDetailsPending,
    addReferenceDetailsRes,
    addReferenceDetailsError,

    addDocumentDetailsPending,
    addDocumentDetailsRes,
    addDocumentDetailsError,

    getAllOnboardingDetailsPending,
    getAllOnboardingDetailsRes,
    getAllOnboardingDetailsError,

    getAllEmpDocsPending,
    getAllEmpDocsRes,
    getAllEmpDocsError,
  } = useSelector(state => state.onboarding);
  const {
    getFilteredDropdownDataError,
    getFilteredDropdownDataRes,
    getFilteredDropdownDataPending
  } = useSelector(state => state.dropdownMaster)
  const {
    getPolicyListPending,
    getPolicyListRes,
    getPolicyListError,
  } = useSelector(state => state.policies)


  const [profileData, setProfileData] = useState()
  const [allOnboardingSteps, setAllOnboardingSteps] = useState('')
  const [sortOrder, setSortOrder] = useState('')
  const [modeProfileModals, setModeProfileModals] = useState(false)
  const [filteredDropdownData, setFilteredDropdownData] = useState({})
  const [isOpenModal, setIsOpenModal] = useState({
    personal_details: false,
    family_details: false,
    education_details: false,
    work_experience_details: false,
    social_details: false,
    reference_details: false,
    policy: false,
    upload_documents: false,
    bank_details: false,
    idCard: false,
  })
  const [personalDetailData, setPersonalDetailData] = useState({});
  const [familyDetails, setFamilyDetails] = useState({});
  const [educationDetails, setEductionDetail] = useState([])
  const [bankDetails, setBankDetails] = useState({})
  const [socialDetails, setSocialDetails] = useState({})
  const [referenceDetails, setReferenceDetails] = useState({})
  const [workExperienceDetails, setWorkExperienceDetails] = useState([])
  const [allEmployeeDocs, setAllEmployeeDocs] = useState('')
  const [allPolicyListData, setAllPolicyListData] = useState('')
  const [updateFields, setUpdateFields] = useState(false)


  useEffect(() => {
    setPersonalDetailData(
      {
        ...personalDetailData,
        "name": profileData ? profileData?.name : '',
        "dob": profileData ? profileData?.dob : '',
        "pan_no": profileData?.pan_no || '',
        "gender": profileData?.gender ? { label: profileData?.gender, value: profileData?.gender } : null,
        "marital_status": profileData?.marital_status ? { label: profileData?.marital_status, value: profileData?.marital_status } : null,
        "personal_mobile": profileData?.personal_mobile || "",
        "personal_email": profileData?.personal_email || "",
        "esic_no": profileData?.esic_no || "",
        "uan_no": profileData?.uan_no || "",
        "aadhar_no": profileData ? profileData?.aadhar_no : "",
        "blood_group": profileData?.marital_status ? { label: profileData?.blood_group, value: profileData?.blood_group } : null,
        "emergency_mobile": profileData?.emergency_mobile || "",
        "present_address": profileData?.present_address || "",
        "present_city": profileData?.present_city || "",
        "present_state": profileData?.present_state || "",
        "present_postal_code": profileData?.present_postal_code || "",
        "permanent_address": profileData?.permanent_address || "",
        "permanent_city": profileData?.permanent_city || "",
        "permanent_state": profileData?.permanent_state || "",
        "permanent_postal_code": profileData?.permanent_postal_code || "",

      }
    );
    setFamilyDetails({
      "fathers_name": profileData?.fathers_name || "",
      "fathers_dob": profileData?.fathers_dob || "",
      "mothers_name": profileData?.mothers_name || "",
      "mothers_dob": profileData?.mothers_dob || "",
      "spouse_name": profileData?.spouse_name || "",
      "spouse_dob": profileData?.spouse_dob || "",
      "spouse_aadhaar": profileData?.spouse_aadhaar || "",
      "first_child_name": profileData?.first_child_name || "",
      "first_child_dob": profileData?.first_child_dob || "",
      "second_child_name": profileData?.second_child_name || "",
      "second_child_dob": profileData?.second_child_dob || "",
    });
    setEductionDetail(profileData?.education_details)
    setBankDetails(
      {
        "ifsc_code": profileData?.ifsc_code || '',
        "bank_name": profileData?.bank_name || '',
        "branch_name": profileData?.branch_name || '',
        "bank_account_holder_name": profileData?.bank_account_holder_name || '',
        "bank_account_no": profileData?.bank_account_no || '',
        "nominee_name": profileData?.nominee_name || '',
        "relation_with_nominee": profileData?.relation_with_nominee || '',
      })
    setSocialDetails(
      {
        "linkedin_id": profileData?.linkedin_id || '',
        "facebook_id": profileData?.facebook_id || '',
        "twitter_id": profileData?.twitter_id || '',
        "instagram_id": profileData?.instagram_id || '',
      }
    );
    setReferenceDetails(
      {
        "first_reference_name": profileData?.reference_details?.[0]?.first_reference_name || '',
        "first_reference_no": profileData?.reference_details?.[0]?.first_reference_no || '',
        "first_reference_email": profileData?.reference_details?.[0]?.first_reference_email || '',
        "second_reference_name": profileData?.reference_details?.[0]?.second_reference_name || '',
        "second_reference_no": profileData?.reference_details?.[0]?.second_reference_no || '',
        "second_reference_email": profileData?.reference_details?.[0]?.second_reference_email || '',
      }
    );

    setWorkExperienceDetails(profileData?.work_experience_details)




  }, [profileData])




  useEffect(() => {
    getProfileData()
    getOnboardingSteps()
    getAllEmployeeDocs()
    handleGetPolicyList()
    getDropdownFilteredData()
  }, [])
  /* Get Employee Profile Data*/
  const getProfileData = () => {
    dispatch(employeeActions.getProfileRequest())
  }

  useEffect(() => {
    if (!getProfilePending) {
      if (getProfileError) {
        return;
      }
      if (getProfileRes) {
        if (getProfileRes?.code === 200) {
          setProfileData(getProfileRes?.data[0])
        }
      }
    }
  }, [
    getProfileRes,
    getProfileError,
    getProfilePending
  ]);


  const handleOpenCloseModal = (type, order, action) => {
    setSortOrder(order)
    if (action === "edit") {
      setModeProfileModals(false)
    } else {
      setModeProfileModals(true)
    }
    setIsOpenModal(pre => ({ ...pre, [type]: !pre[type] }))

  }

  const handleNextPreOnClick = (type, order) => {
    setSortOrder(order)
    setIsOpenModal(prev => {
      const resetState = Object.keys(prev).reduce((acc, key) => {
        acc[key] = false;
        return acc;
      }, {});
      return { ...resetState, [type]: true };

    });
  };

  const getOnboardingSteps = () => {
    let data = {
      // is_active: true,
      view_profile: true,
    }
    dispatch(onboardingActions.getAllOnboardingDetailsRequest(data))
  }

  useEffect(() => {
    if (!getAllOnboardingDetailsPending) {
      if (getAllOnboardingDetailsError) {
        return;
      }
      if (getAllOnboardingDetailsRes) {
        if (getAllOnboardingDetailsRes?.code === 200) {
          setAllOnboardingSteps(getAllOnboardingDetailsRes?.data)
        }
      }
    }
  }, [
    getAllOnboardingDetailsRes,
    getAllOnboardingDetailsError,
    getAllOnboardingDetailsPending
  ]);

  const getAllEmployeeDocs = () => {
    dispatch(onboardingActions.getAllEmpDocsRequest())
  }

  useEffect(() => {
    if (!getAllEmpDocsPending) {
      if (getAllEmpDocsError) {
        return;
      }
      if (getAllEmpDocsRes) {
        if (getAllEmpDocsRes?.code === 200) {
          setAllEmployeeDocs(getAllEmpDocsRes?.data)
        }
      }
    }
  }, [
    getAllEmpDocsRes,
    getAllEmpDocsError,
    getAllEmpDocsPending
  ]);

  const handleGetPolicyList = () => {
    dispatch(policiesActions.getPolicyListRequest());
  }

  useEffect(() => {
    if (!getPolicyListPending) {
      if (getPolicyListError) {
        return;
      }
      if (getPolicyListRes) {
        if (getPolicyListRes?.code === 200) {
          const getPolicyListResData = getPolicyListRes?.data
          setAllPolicyListData(getPolicyListResData)


        }
      }
    }
  }, [
    getPolicyListRes,
    getPolicyListError,
    getPolicyListPending
  ]);


  const getDropdownFilteredData = () => {
    const category = ['marital_status', 'blood_group', 'gender', 'work_experience']
    let params = { category: category }

    dispatch(dropdownMasterActions.getFilteredDropdownDataRequest(params))

  }

  useEffect(() => {
    if (!getFilteredDropdownDataPending) {
      if (getFilteredDropdownDataError) {
        return;
      }
      if (getFilteredDropdownDataRes) {
        if (getFilteredDropdownDataRes?.code === 200) {
          const updateData = setFilterDropdownData(getFilteredDropdownDataRes?.data?.data)
          setFilteredDropdownData(updateData);
        }
      }
    }
  }, [
    getFilteredDropdownDataRes,
    getFilteredDropdownDataError,
    getFilteredDropdownDataPending
  ]);


  /*----------------------------Update Onboarding steps---------------------------------- */


  useEffect(() => {
    // personal details
    if (!addPersonalDetailsPending) {
      if (addPersonalDetailsError) {
        dispatch(onboardingActions.handleClearPersonalDetails('HANDLE_CLEAR_ADD_PERSONAL_DETAILS'))
        return;
      }
      if (addPersonalDetailsRes) {
        if (addPersonalDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearPersonalDetails('HANDLE_CLEAR_ADD_PERSONAL_DETAILS'))
          getProfileData()

        }
      }
    };
    // family details

    if (!addFamilyDetailsPending) {
      if (addFamilyDetailsError) {
        dispatch(onboardingActions.handleClearFamilyDetails('HANDLE_CLEAR_ADD_FAMILY_DETAILS'))
        return;
      }
      if (addFamilyDetailsRes) {
        if (addFamilyDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearFamilyDetails('HANDLE_CLEAR_ADD_FAMILY_DETAILS'))
          getProfileData()
        }
      }
    }
    if (!addEducationDetailsPending) {
      if (addEducationDetailsError) {
        dispatch(onboardingActions.handleClearEducationDetails('HANDLE_CLEAR_ADD_EDUCATION_DETAILS'))
        return;
      }
      if (addEducationDetailsRes) {
        if (addEducationDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearEducationDetails('HANDLE_CLEAR_ADD_EDUCATION_DETAILS'))
          getProfileData()
        }
      }
    }
    // bank details
    if (!addBankDetailsPending) {
      if (addBankDetailsError) {
        dispatch(onboardingActions.handleClearBankDetails('HANDLE_CLEAR_ADD_BANK_DETAILS'))
        return;
      }
      if (addBankDetailsRes) {
        if (addBankDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearBankDetails('HANDLE_CLEAR_ADD_BANK_DETAILS'))
          getProfileData()
        }
      }
    }
    // social details
    if (!addSocialDetailsPending) {
      if (addSocialDetailsError) {
        dispatch(onboardingActions.handleClearSocialDetails('HANDLE_CLEAR_ADD_SOCIAL_DETAILS'))
        return;
      }
      if (addSocialDetailsRes) {
        if (addSocialDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearSocialDetails('HANDLE_CLEAR_ADD_SOCIAL_DETAILS'))
          getProfileData()
        }
      }
    }
    // social details
    if (!addWorkExperienceDetailsPending) {
      if (addWorkExperienceDetailsError) {
        dispatch(onboardingActions.handleClearWorkExperienceDetails('HANDLE_CLEAR_ADD_WORK_EXPERIENCE_DETAILS'))
        return;
      }
      if (addWorkExperienceDetailsRes) {
        if (addWorkExperienceDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearWorkExperienceDetails('HANDLE_CLEAR_ADD_WORK_EXPERIENCE_DETAILS'))
          getProfileData()
        }
      }
    }
    // reference details
    if (!addReferenceDetailsPending) {
      if (addReferenceDetailsError) {
        dispatch(onboardingActions.handleClearReferenceDetails('HANDLE_CLEAR_ADD_REFERENCE_DETAILS'))
        return;
      }
      if (addReferenceDetailsRes) {
        if (addReferenceDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearReferenceDetails('HANDLE_CLEAR_ADD_REFERENCE_DETAILS'))
          getProfileData()
        }
      }
    }

    // document details
    if (!addDocumentDetailsPending) {
      if (addDocumentDetailsError) {
        dispatch(onboardingActions.handleClearDocumentDetails('HANDLE_CLEAR_ADD_DOCUMENT_DETAILS'))
        return;
      }
      if (addDocumentDetailsRes) {
        if (addDocumentDetailsRes?.code === 200) {
          dispatch(onboardingActions.handleClearDocumentDetails('HANDLE_CLEAR_ADD_DOCUMENT_DETAILS'))
          getAllEmployeeDocs()
          getProfileData()
        }
      }
    }
  }, [
    addPersonalDetailsRes,
    addPersonalDetailsError,
    addPersonalDetailsPending,

    addFamilyDetailsRes,
    addFamilyDetailsError,
    addFamilyDetailsPending,

    addEducationDetailsPending,
    addEducationDetailsRes,
    addEducationDetailsError,


    addBankDetailsRes,
    addBankDetailsError,
    addBankDetailsPending,

    addSocialDetailsRes,
    addSocialDetailsError,
    addSocialDetailsPending,

    addWorkExperienceDetailsPending,
    addWorkExperienceDetailsRes,
    addWorkExperienceDetailsError,

    addReferenceDetailsRes,
    addReferenceDetailsError,
    addReferenceDetailsPending,

    addDocumentDetailsRes,
    addDocumentDetailsError,
    addDocumentDetailsPending,
  ]);

  const addPersonalDetails = (params) => {
    const payload = {
      query_type: "personal_details",
      step_id: "1",
      ...params
    }
    dispatch(onboardingActions.addPersonalDetailsRequest(payload))
  };


  const addFamilyDetails = (params) => {
    const payload = {
      query_type: "family_details",
      step_id: "2",
      ...params
    }
    dispatch(onboardingActions.addFamilyDetailsRequest(payload))
  };
  const addEductionDetails = (params) => {
    const payload = {
      query_type: "education_details",
      step_id: "3",
      ...params
    }
    dispatch(onboardingActions.addEducationDetailsRequest(payload))
  };

  const addBankDetails = (params) => {
    const payload = {
      query_type: "bank_details",
      step_id: "4",
      ...params
    }
    dispatch(onboardingActions.addBankDetailsRequest(payload))
  };

  const addSocialDetails = (params) => {
    const payload = {
      query_type: "social_details",
      step_id: "5",
      ...params
    }
    dispatch(onboardingActions.addSocialDetailsRequest(payload))
  };

  const addReferenceDetails = (params) => {
    const payload = {
      query_type: "reference_details",
      step_id: "7",
      reference_details: [params]
    }
    dispatch(onboardingActions.addReferenceDetailsRequest(payload))
  };

  const addWorkExperienceDetails = (params) => {
    const payload = {
      query_type: "work_experience_details",
      step_id: "6",
      ...params
    }
    dispatch(onboardingActions.addWorkExperienceDetailsRequest(payload))

  };

  const uploadDocuments = (params) => {
    let formData = new FormData()
    formData.append("aadhar_card", params?.aadhar_card || null)
    formData.append("aadhar_card_back", params?.aadhar_card_back || null)
    formData.append("passport", params?.passport || null)
    formData.append("passport_back", params?.passport_back || null)
    formData.append("pancard", params?.pancard || null)
    formData.append("driving_license", params?.driving_license || null)
    formData.append("resume", params?.resume || null)
    formData.append("passport_size_photo", params?.passport_size_photo || null)
    formData.append("secondary_marksheet", params?.secondary_marksheet || null)
    formData.append("senior_secondary_marksheet", params?.senior_secondary_marksheet || null)
    formData.append("ITI_marksheet", params?.ITI_marksheet || null)
    formData.append("diploma_marksheet", params?.diploma_marksheet || null)
    formData.append("graduation_marksheet", params?.graduation_marksheet || null)
    formData.append("post_graduation_marksheet", params?.post_graduation_marksheet || null)
    formData.append("bank_passbook", params?.bank_passbook || null)
    formData.append("previous_company_experience_letter", params?.previous_company_experience_letter || null)
    formData.append("previous_company_payslip", params?.previous_company_payslip || null)
    formData.append("police_verification", params?.police_verification || null)
    formData.append("medical_certificate", params?.medical_certificate || null)
    formData.append("bgv_certificate", params?.bgv_certificate || null)
    formData.append("query_type", "document_details")
    formData.append("step_id", "8")

    dispatch(onboardingActions.addDocumentDetailsRequest(formData))

  };




  return (
    <>
      <ProfileInfo
        profileData={profileData}
      />
      <ProfileEditDocCard
        handleOpenCloseModal={handleOpenCloseModal}
        allOnboardingSteps={allOnboardingSteps}
      />
      <ProfileDetailCards
        handleOpenCloseModal={handleOpenCloseModal}
        personalDetailData={personalDetailData}
        familyDetails={familyDetails}
        allOnboardingSteps={allOnboardingSteps}
        educationDetails={educationDetails}
        bankDetails={bankDetails}
        socialDetails={socialDetails}
        referenceDetails={referenceDetails}
        workExperienceDetails={workExperienceDetails}
        profileData={profileData}




      />

      {isOpenModal?.personal_details &&
        <ProfileDetail
          openModel={isOpenModal?.personal_details}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          profileData={profileData}
          personalDetailData={personalDetailData}
          setPersonalDetailData={setPersonalDetailData}
          sortOrder={sortOrder}
          allOnboardingSteps={allOnboardingSteps}
          modeProfileModals={modeProfileModals}
          filteredDropdownData={filteredDropdownData}
          addPersonalDetails={addPersonalDetails}
        />}
      {isOpenModal?.family_details &&
        <FamilyDetail
          openModel={isOpenModal?.family_details}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          profileData={profileData}
          familyDetails={familyDetails}
          setFamilyDetails={setFamilyDetails}
          sortOrder={sortOrder}
          allOnboardingSteps={allOnboardingSteps}
          modeProfileModals={modeProfileModals}
          addFamilyDetails={addFamilyDetails}
        />}
      {isOpenModal?.education_details &&
        <EducationDetail
          openModel={isOpenModal?.education_details}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          profileData={profileData}
          sortOrder={sortOrder}
          allOnboardingSteps={allOnboardingSteps}
          modeProfileModals={modeProfileModals}
          addEductionDetails={addEductionDetails}

        />}
      {isOpenModal?.bank_details && <BankDetail
        openModel={isOpenModal?.bank_details}
        handleOpenClose={handleOpenCloseModal}
        handleNextPreOnClick={handleNextPreOnClick}
        bankDetails={bankDetails}
        setBankDetails={setBankDetails}
        sortOrder={sortOrder}
        allOnboardingSteps={allOnboardingSteps}
        modeProfileModals={modeProfileModals}
        profileData={profileData}
        addBankDetails={addBankDetails}
      />}
      {isOpenModal?.social_details &&
        <SocialDetail
          openModel={isOpenModal?.social_details}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          sortOrder={sortOrder}
          allOnboardingSteps={allOnboardingSteps}
          profileData={profileData}
          modeProfileModals={modeProfileModals}
          addSocialDetails={addSocialDetails}
        />}


      {isOpenModal?.reference_details &&
        <ReffrenceDetail
          openModel={isOpenModal?.reference_details}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          sortOrder={sortOrder}
          allOnboardingSteps={allOnboardingSteps}
          profileData={profileData}
          modeProfileModals={modeProfileModals}
          addReferenceDetails={addReferenceDetails}
        />}
      {isOpenModal?.work_experience_details && <WorkExpDetail
        openModel={isOpenModal?.work_experience_details}
        handleOpenClose={handleOpenCloseModal}
        handleNextPreOnClick={handleNextPreOnClick}
        sortOrder={sortOrder}
        allOnboardingSteps={allOnboardingSteps}
        profileData={profileData}
        modeProfileModals={modeProfileModals}
        addWorkExperienceDetails={addWorkExperienceDetails}
      />}

      {isOpenModal?.policy &&
        <Policies
          openModel={isOpenModal?.policy}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          allPolicyListData={allPolicyListData}
          profileData={profileData}
          sortOrder={sortOrder}
          allOnboardingSteps={allOnboardingSteps}
          modeProfileModals={modeProfileModals}
        />}

      {isOpenModal?.upload_documents && <DocumentDetail
        openModel={isOpenModal?.upload_documents}
        handleOpenClose={handleOpenCloseModal}
        allEmployeeDocs={allEmployeeDocs}
        handleNextPreOnClick={handleNextPreOnClick}
        sortOrder={sortOrder}
        allOnboardingSteps={allOnboardingSteps}
        modeProfileModals={modeProfileModals}
        uploadDocuments={uploadDocuments}
      />}

      {isOpenModal?.idCard && <IdCard
        openModel={isOpenModal?.idCard}
        handleOpenClose={handleOpenCloseModal}
      />}


    </>
  )
}

export default Profile
