import React, { useEffect, useRef, useState } from 'react'
import { PolicyDetails } from '../../employee/onboarding-process';

import ProfileHeader from './ProfileHeader';

const Policies = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick, allPolicyListData,
        profileData,
        updatePolicySign,
        allOnboardingSteps,
        sortOrder,
        modeProfileModals
    } = props




    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="policyDetailshh"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title fs-5 fw-bold text-primary">Policy Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("policy")}
                    />
                </div>
                <div className="offcanvas-body viewcnv p-0">
                    <>

                        <ProfileHeader
                            sortOrder={sortOrder}
                            allOnboardingSteps={allOnboardingSteps}
                            handleNextPreOnClick={handleNextPreOnClick}
                        />
                        <PolicyDetails
                            allPolicyListData={allPolicyListData}
                            profileData={profileData}
                            modeProfileModals={modeProfileModals}
                        />






                     
                    </>

                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end">
                        {/* <button type="button" className="btn btn-secondary">Back</button> */}

                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default Policies
